//TicketPurchase.js
import React, { useState } from 'react';
import { StyleSheet, Text, View, Image, TextInput, Alert, TouchableOpacity } from 'react-native';
//ticket data
import globoTickets from './TicketsDB';

//Ticket Purchase component
//use route parameters to pass information along the other components to
//present the correct ticket to purchase
const TicketPurchase = ({ route, navigation }) => {
    //hooks for ticket state
    const [ticketQuantity, setTicketQuantity] = useState('1'); //use string because value props only accepts string value
    //use the route params to get the ticket id
    const { tickId } = route.params;
    //get the selected event
    const selectedEvent = globoTickets.find(tickets => tickets.eventId === tickId);

    //event for pressing a button
    const purchase = () => {
        var price = selectedEvent.price * ticketQuantity;
        Alert.alert(`Your cost is ${price}`);
        navigation.navigate('Home');
    };

    return (
        <View style={styles.container}>
            <Text style={styles.title}>{selectedEvent.event}</Text>
            <Image
                style={styles.ticketimage}
                source={selectedEvent.image}
            />
            <Text style={styles.shortdescription}>
                {selectedEvent.shortDescription}
            </Text>
            <Text style={styles.description}>
                {selectedEvent.description}
            </Text>
            <View style={styles.purchaseRow}>
                <Text style={styles.shortdescription}>Quantity :</Text>
                <TextInput
                    style={styles.quantityinput}
                    onChangeText={quantity => setTicketQuantity(quantity)}
                    value={ticketQuantity}
                    selectTextOnFocus={true}
                    keyboardType='numeric'
                />
            </View>
            <Text style={styles.price}>
                Total Price: ${selectedEvent.price * ticketQuantity}
            </Text>
            <TouchableOpacity
                onPress={purchase}
                style={styles.button}
            >
                <Text style={styles.buttontext}>Buy Now</Text>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'column',
    alignItems: 'center',
    paddingTop: 10,
  },
  purchaseRow: {
    flexDirection: 'row',
  },
  button: {
    borderTopWidth: 1,
    borderBottomWidth: 1,
    width: '60%',
  },
  title: {
    fontFamily: 'Ubuntu-Regular',
    fontWeight: 'bold',
    paddingBottom: 10,
  },
  ticketimage: {
    width: '100%',
    height: 180,
  },
  shortdescription: {
    fontFamily: 'Ubuntu-Regular',
    fontWeight: 'bold',
    paddingTop: 5,
    textAlignVertical: 'center',
  },
  description: {
    textAlign: 'left',
    fontFamily: 'Ubuntu-Light',
    fontWeight: '600',
    padding: 10,
  },
  price: {
    fontFamily: 'Ubuntu-Regular',
    fontWeight: 'bold',
    paddingTop: 5,
    paddingBottom: 10,
  },
  buttontext: {
    fontFamily: 'Ubuntu-Regular',
    fontWeight: 'bold',
    textAlign: 'center',
    padding: 5,
  },
  quantityinput: {
    borderWidth: 1,
    fontFamily: 'Ubuntu-Regular',
    fontWeight: 'bold',
    height: 38,
    width: 40,
    marginLeft: 25,
  },
});

export default TicketPurchase;